import * as THREE from 'three';
import { OculusHandModel } from 'three/examples/jsm/webxr/OculusHandModel.js';
import { createText } from 'three/examples/jsm/webxr/Text2D.js';
import { XRControllerModelFactory } from 'three/examples/jsm/webxr/XRControllerModelFactory.js';
import eta2Actis from './Eta2Actis.js';
import xr from './xr.js';
var createScene = function () {
    var scene = new THREE.Scene();
    scene.background = new THREE.Color(0x444444);
    scene.add(new THREE.HemisphereLight(0x808080, 0x606060));
    var light = new THREE.DirectionalLight(0xffffff);
    light.position.set(0, 6, 0);
    light.castShadow = true;
    light.shadow.camera.top = 2;
    light.shadow.camera.bottom = -2;
    light.shadow.camera.right = 2;
    light.shadow.camera.left = -2;
    light.shadow.mapSize.set(4096, 4096);
    scene.add(light);
    // controllers
    var controller1 = xr.getController(0);
    scene.add(controller1);
    var controller2 = xr.getController(1);
    scene.add(controller2);
    var controllerModelFactory = new XRControllerModelFactory();
    // Hand 1
    var controllerGrip1 = xr.getControllerGrip(0);
    controllerGrip1.add(controllerModelFactory.createControllerModel(controllerGrip1));
    scene.add(controllerGrip1);
    var hand1 = xr.getHand(0);
    var handModel1 = new OculusHandModel(hand1);
    hand1.add(handModel1);
    scene.add(hand1);
    // Hand 2
    var controllerGrip2 = xr.getControllerGrip(1);
    controllerGrip2.add(controllerModelFactory.createControllerModel(controllerGrip2));
    scene.add(controllerGrip2);
    var hand2 = xr.getHand(1);
    var handModel2 = new OculusHandModel(hand2);
    hand2.add(handModel2);
    scene.add(hand2);
    var exitText = createText('Exiting session...', 0.04);
    exitText.position.set(0, 1.5, -0.6);
    exitText.visible = false;
    scene.add(exitText);
    eta2Actis.position.set(-1.5, -1.5, 0);
    scene.add(eta2Actis);
    return scene;
};
var scene = createScene();
export default scene;
